'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import CardLogin_Login from '@/app/(auth)/register/components/cardLogin_Login';
import CardLogin_Register from '@/app/(auth)/register/components/cardLogin_Register';

export default function Register() {
    const [activeTab, setActiveTab] = useState<'login' | 'register'>('register');

    return (
        <div className="min-h-screen flex items-center justify-center bg-ccDarkGray">
            <div className="bg-ccGray p-8 rounded-xl shadow-md w-full max-w-lg border border-ccLightGray">
                {/* Tabs */}
                <div className="relative flex items-center justify-between px-4 py-2 bg-ccLightGray rounded-xl text-white">
                    <motion.div
                        layout
                        transition={{ type: 'spring', stiffness: 300, damping: 30 }}
                        className="absolute top-1 bottom-1 left-1 w-1/2 bg-white rounded-lg z-0"
                        style={{
                            left: activeTab === 'login' ? '4px' : '50%',
                            transform: activeTab === 'login' ? 'translateX(0%)' : 'translateX(-4px)',
                        }}
                    />

                    <button
                        onClick={() => setActiveTab('login')}
                        className={`w-1/2 text-xl py-2 px-4 z-10 transition-colors duration-300 ${
                            activeTab === 'login' ? 'text-black' : 'text-white'
                        }`}
                    >
                        Iniciar sesión
                    </button>
                    <button
                        onClick={() => setActiveTab('register')}
                        className={`w-1/2 text-xl py-2 px-4 z-10 transition-colors duration-300 ${
                            activeTab === 'register' ? 'text-black' : 'text-white'
                        }`}
                    >
                        Registrarse
                    </button>
                </div>

                {/* Contenido dinámico con animación */}
                <div className="mt-6 relative min-h-[200px]">
                    <AnimatePresence mode="wait">
                        {activeTab === 'login' ? (
                            <motion.div
                                key="login"
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                exit={{ opacity: 0, y: -20 }}
                                transition={{ duration: 0.3 }}
                                className="absolute w-full"
                            >
                                <CardLogin />
                            </motion.div>
                        ) : (
                            <motion.div
                                key="register"
                                initial={{ opacity: 0, y: 20 }}
                                animate={{ opacity: 1, y: 0 }}
                                exit={{ opacity: 0, y: -20 }}
                                transition={{ duration: 0.3 }}
                                className="absolute w-full"
                            >
                                <CardLogin />
                            </motion.div>
                        )}
                    </AnimatePresence>
                </div>
            </div>
        </div>
    );
}
